import { EditorState } from 'draft-js';
import { connect } from 'react-redux';
import _ from 'lodash';
import { createGUID } from 'src/utils/guid';
import { formatAgentChatUploadUrl, formatAgentChatDownloadUrl } from 'src/utils/helper';
import * as enumFontSize from '../../constants/enums/fontSize';
import TextEditor from '../../components/TextEditor/TextEditor';
import * as selectors from '../../reducers/selectors';
import * as actions from '../../actions/agentChat';
import * as agentActions from '../../actions/agent';
import * as chatBusiness from '../Business/agentChat';
import * as editor from '../../utils/editor';
import * as configActions from '../../actions/config';
import { isImage } from '../../utils/seq';
import * as str from '../../utils/str';
import { noSelectedForAgent } from '../../constants/enums';

let previousState = {};
const emptyEditorState = EditorState.createEmpty();
const emptyEditorStateFocused = editor.createEmpty();
const mapStateToProps = (state) => {
  const getUploadUrl = () => formatAgentChatUploadUrl(
    selectors.getFileUploadUrl(state),
    selectors.getCurrentAgentSession(state),
    selectors.getSiteId(state),
    1,
    createGUID()
  );
  if (_.isNull(selectors.getSelectedIdForAgents(state)) ||
      selectors.getSelectedIdForAgents(state) === noSelectedForAgent) {
    return {
      id: noSelectedForAgent,
      isAgent: false,
      sendToIds: [],
      editorState: selectors.getAgentChatEditorState(state, noSelectedForAgent) || emptyEditorState,
      sendType: selectors.getSendType(state),
      cannedMessages: [],
      disabled: true,
      siteInfo: {},
      textDirectionIfRTL: selectors.textDirectionIfRTL(state),
      selectedFontSize: enumFontSize.middle,
      disablePaste: true,
      files: [],
      getUploadUrl,
    };
  }
  const [isAgent, id] = chatBusiness.getAgentOrDepartmentId(state);
  if (!chatBusiness.isActiveStatus(state, isAgent, id)) {
    const newState = Object.assign({}, previousState);
    newState.disabled = true;
    return newState;
  }
  const disabled = !chatBusiness.ifChatIsAvalable(state, isAgent, id);
  let editorState = emptyEditorState;
  if (!disabled) {
    editorState = selectors.getAgentChatEditorState(state, id);
    if (!editorState) editorState = emptyEditorStateFocused;
  }
  previousState = {
    id,
    isAgent,
    sendToIds: chatBusiness.getSendToIds(state, isAgent, id),
    editorState,
    sendType: selectors.getSendType(state),
    cannedMessages: [],
    disabled,
    siteInfo: {
      uploadUrl: selectors.getFileUploadUrl(state),
      downloadUrl: selectors.getFileDownloadUrl(state),
      siteId: selectors.getSiteId(state),
      sessionId: selectors.getCurrentAgentSession(state),
      maxFileSize: selectors.getAgentChatFileSize(state),
    },
    textDirectionIfRTL: selectors.textDirectionIfRTL(state),
    selectedFontSize: enumFontSize.middle,
    disablePaste: true,
    getUploadUrl,
    files: selectors.getAgentChatUploadFiles(state, id),
  };
  return previousState;
};

const mergeProps = (stateProps, dispatchProps) => {
  const { dispatch } = dispatchProps;
  const dispatchObject = {
    onStart(chatId, filename, guid) {
      dispatch(actions.startUpload(chatId, filename, guid));
    },
    onProgress(chatId, percentage, filename, guid) {
      dispatch(actions.uploadFile(chatId, percentage, filename, guid));
    },
    onSuccess(chatId, filename, fid, respond, sendToIds) {
      dispatch(actions.uploadFile(chatId, 100, filename, fid));
      const url = formatAgentChatDownloadUrl(
        stateProps.siteInfo.downloadUrl,
        stateProps.siteInfo.sessionId,
        stateProps.siteInfo.siteId,
        1,
        respond.data.guid,
        filename
      );
      const guid = respond.data.guid;
      dispatch(actions.appendFile(
        chatBusiness.filterSendToIds(chatId, sendToIds, stateProps.isAgent),
        url,
        filename,
        isImage(filename)
      ));
      sendToIds.forEach((agentId) => {
        dispatch(agentActions.sendFileToAgent(agentId, filename, guid));
      });
    },
    onCancel(chatId, filename, guid) {
      dispatch(actions.cancelUpload(chatId, filename, guid));
    },
    setErrorMessage(chatId, message) {
      dispatch(actions.setErrorMessageForAgentChat(chatId, message));
    },
    onChange(id, editorState) {
      dispatch(actions.updateContent(id, editorState));
    },
    onSendOut(id, sendToIds, editorState) {
      const lines = editor.convertToList(editorState);
      if (lines.length > 0 && !str.isEmptyMessage(lines[0])) {
        dispatch(actions.updateContent(id, editor.createEmpty()));
        lines.forEach((line) => {
          if (typeof line === 'string') {
            dispatch(actions.appendOwnMessage(chatBusiness.filterSendToIds(id,
                                                                           sendToIds,
                                                                           stateProps.isAgent),
                                              line));
            sendToIds.forEach((agentId) => {
              dispatch(agentActions.sendPrivateMessageToAgent(agentId, line));
            });
          }
        });
      }
    },
    onSendTypeSelect(type) {
      dispatch(configActions.updateSendType(type));
      dispatch(agentActions.setPreference());
    },
  };
  return {
    ...stateProps,
    ...dispatchObject,
  };
};

export default connect(mapStateToProps, null, mergeProps)(TextEditor);



