import React from 'react';
import classnames from 'classnames';
// import { Editor, EditorState, RichUtils, ContentState } from 'draft-js';
import { Editor, EditorState, RichUtils } from 'draft-js';
// import DraftPasteProcessor from 'draft-js/lib/DraftPasteProcessor';
import { stateFromHTML } from 'draft-js-import-html';
import { stateToHTML } from 'draft-js-export-html';
import Link from 'src/components/Link/Link';
import Checkbox from '../../Checkbox/Checkbox';
import memoryStore from '../../../services/memoryStore';
import ModalStyle from '../Modal.css';

export const memoryKey = 'sendTranscriptBody';
class SendTranscriptBody extends React.Component {
  constructor(props) {
    super(props);

    let cacheState = memoryStore.get(`${memoryKey}_${props.transcriptBodyAdditionInfo.chatId}`, null, null);
    if (!cacheState) {
      const editorState = EditorState.createWithContent(stateFromHTML(props.input.value));
      cacheState = {
        ifBodyLoading: !editorState.getCurrentContent().hasText(),
        editorState,
        bodyContentArray: [props.input.value, '', '', '', '', '', ''],
        additionalInfoIfOpen: false,
        ifHaveVisitorInfo: false,
        ifHaveNavigation: false,
        ifHaveGEOInfo: false,
        ifHaveClientInfo: false,
        ifHaveRating: false,
        ifHaveAgentComment: false,
        renderWithProps: true,
      };
      memoryStore.set(`${memoryKey}_${this.props.transcriptBodyAdditionInfo.chatId}`, cacheState);
    }
    this.state = cacheState;
    this.onChange = this.onChange.bind(this);
    this.onTab = this.onTab.bind(this);
    this.toggleAdditionalInfo = this.toggleAdditionalInfo.bind(this);
    this.toggleVisitorInfo = this.toggleVisitorInfo.bind(this);
  }
  componentWillReceiveProps(nextProps) {
    const cacheState = memoryStore.get(`${memoryKey}_${nextProps.transcriptBodyAdditionInfo.chatId}`, null, null);
    if (nextProps.input.value !== this.props.input.value && this.state.ifBodyLoading) {
      const editorState = EditorState.createWithContent(stateFromHTML(nextProps.input.value));
      cacheState.editorState = editorState;
      cacheState.bodyContentArray = [nextProps.input.value, '', '', '', '', '', ''];
      this.setState({
        ifBodyLoading: false,
        editorState: cacheState.editorState,
        bodyContentArray: cacheState.bodyContentArray,
      });
      memoryStore.set(`${memoryKey}_${this.props.transcriptBodyAdditionInfo.chatId}`, cacheState);
    } else {
      this.setState({
        ifBodyLoading: false,
        editorState: cacheState.editorState,
        bodyContentArray: cacheState.bodyContentArray,
      });
    }
  }

  onChange(editorState) {
    const cacheState = memoryStore.get(`${memoryKey}_${this.props.transcriptBodyAdditionInfo.chatId}`);
    this.props.input.onChange(stateToHTML(editorState.getCurrentContent()));
    this.setState({
      editorState,
    });
    cacheState.editorState = editorState;
    memoryStore.set(`${memoryKey}_${this.props.transcriptBodyAdditionInfo.chatId}`, cacheState);
  }
  onTab(e) {
    const maxDepth = 4;
    this.onChange(RichUtils.onTab(e, this.state.editorState, maxDepth));
  }

  // handleKeyCommand(command) {
  //   const { editorState } = this.state;
  //   const newState = RichUtils.handleKeyCommand(editorState, command);
  //   if (newState) {
  //     this.onChange(newState);
  //     return true;
  //   }
  //   return false;
  // }
  toggleAdditionalInfo() {
    const cacheState = memoryStore.get(`${memoryKey}_${this.props.transcriptBodyAdditionInfo.chatId}`);
    this.setState({
      additionalInfoIfOpen: !this.state.additionalInfoIfOpen,
    });
    cacheState.additionalInfoIfOpen = !this.state.additionalInfoIfOpen;
    memoryStore.set(`${memoryKey}_${this.props.transcriptBodyAdditionInfo.chatId}`, cacheState);
  }
  toggleVisitorInfo(additionInfoId) {
    const cacheState = memoryStore.get(`${memoryKey}_${this.props.transcriptBodyAdditionInfo.chatId}`);
    // this.props.change('additionalInfo_visitorInfo', value);
    const visitorInfo = this.props.transcriptBodyAdditionInfo.transcriptVisitorInfo;
    const navigation = this.props.transcriptBodyAdditionInfo.transcriptNavigation;
    const geoInfo = this.props.transcriptBodyAdditionInfo.transcriptGEOInfo;
    const clientInfo = this.props.transcriptBodyAdditionInfo.transcriptClientInfo;
    const rating = this.props.transcriptBodyAdditionInfo.transcriptRating;
    const agentComment = this.props.transcriptBodyAdditionInfo.transcriptAgentComment;
    const bodyContentArray = this.state.bodyContentArray;
    switch (additionInfoId) {
      case '1': {
        if (!this.state.ifHaveVisitorInfo) {
          // bodyContentArray.splice(1, 0, visitorInfo);
          bodyContentArray[1] = visitorInfo;
        } else {
          // bodyContentArray.splice(1, 1);
          bodyContentArray[1] = '';
        }
        this.setState({
          ifHaveVisitorInfo: !this.state.ifHaveVisitorInfo,
          bodyContentArray,
        });
        cacheState.ifHaveVisitorInfo = !this.state.ifHaveVisitorInfo;
        break;
      }
      case '2': {
        if (!this.state.ifHaveNavigation) {
          bodyContentArray[2] = navigation;
        } else {
          bodyContentArray[2] = '';
        }
        this.setState({
          ifHaveNavigation: !this.state.ifHaveNavigation,
          bodyContentArray,
        });
        cacheState.ifHaveNavigation = !this.state.ifHaveNavigation;
        break;
      }
      case '3': {
        if (!this.state.ifHaveGEOInfo) {
          bodyContentArray[3] = geoInfo;
        } else {
          bodyContentArray[3] = '';
        }
        this.setState({
          ifHaveGEOInfo: !this.state.ifHaveGEOInfo,
          bodyContentArray,
        });
        cacheState.ifHaveGEOInfo = !this.state.ifHaveGEOInfo;
        break;
      }
      case '4': {
        if (!this.state.ifHaveClientInfo) {
          bodyContentArray[4] = clientInfo;
        } else {
          bodyContentArray[4] = '';
        }
        this.setState({
          ifHaveClientInfo: !this.state.ifHaveClientInfo,
          bodyContentArray,
        });
        cacheState.ifHaveClientInfo = !this.state.ifHaveClientInfo;
        break;
      }
      case '5': {
        if (!this.state.ifHaveRating) {
          bodyContentArray[5] = rating;
        } else {
          bodyContentArray[5] = '';
        }
        this.setState({
          ifHaveRating: !this.state.ifHaveRating,
          bodyContentArray,
        });
        cacheState.ifHaveRating = !this.state.ifHaveRating;
        break;
      }
      case '6': {
        if (!this.state.ifHaveAgentComment) {
          bodyContentArray[6] = agentComment;
        } else {
          bodyContentArray[6] = '';
        }
        this.setState({
          ifHaveAgentComment: !this.state.ifHaveAgentComment,
          bodyContentArray,
        });
        cacheState.ifHaveAgentComment = !this.state.ifHaveAgentComment;
        break;
      }
      default: break;
    }
    const afterAddVisitorInfoHTML = bodyContentArray.join(' '); // this.state.bodyContentArray.join(' ');
    let afterAddVisitorInfoEditorState = EditorState.createWithContent(
      stateFromHTML(afterAddVisitorInfoHTML)
    );
    afterAddVisitorInfoEditorState = EditorState.moveFocusToEnd(afterAddVisitorInfoEditorState);

    this.setState({
      editorState: afterAddVisitorInfoEditorState,
      bodyContentArray,
    });
    cacheState.editorState = afterAddVisitorInfoEditorState;
    cacheState.bodyContentArray = bodyContentArray;
    memoryStore.set(`${memoryKey}_${this.props.transcriptBodyAdditionInfo.chatId}`, cacheState);
  }

  render() {
    const { editorState } = this.state;

    // If the user changes block type before entering any text, we can
    // either style the placeholder or hide it. Let's just hide it now.
    /**
     * @todo
     * className is unused and thus comment out. Check if there is any issue here.
     */
    // let className = 'RichEditor-editor';
    const contentState = editorState.getCurrentContent();
    if (!contentState.hasText()) {
      // if (contentState.getBlockMap().first().getType() !== 'unstyled') {
        // className += ' RichEditor-hidePlaceholder';
      // }
    }


    return (
      <div className={ModalStyle.bodyLayout}>
        <div className={ModalStyle.formTranscriptAdditionalInfo}>
          <Link onClick={this.toggleAdditionalInfo}>Additional Information</Link>
          {
            this.state.additionalInfoIfOpen ?
              <div className={ModalStyle.additionalInfoContent}>
                <Checkbox
                  name="additionalInfo_visitorInfo"
                  input={{
                    value: this.state.ifHaveVisitorInfo,
                    onChange: () => this.toggleVisitorInfo('1'),
                  }}
                  text="Visitor Info (First Visit Time, Chat Times, Visit Times)"
                  className={ModalStyle.transcriptAdditionInfo}
                />
                <Checkbox
                  name="additionalInfo_navigation"
                  input={{
                    value: this.state.ifHaveNavigation,
                    onChange: () => this.toggleVisitorInfo('2'),
                  }}
                  text="Navigation (Referrer, Search Engine, Search Keywords, Landing Page,
                  Navigation Pages)"
                  className={ModalStyle.transcriptAdditionInfo}
                />
                <Checkbox
                  name="additionalInfo_geo"
                  input={{
                    value: this.state.ifHaveGEOInfo,
                    onChange: () => this.toggleVisitorInfo('3'),
                  }}
                  text="GEO Info (IP, Country, State, City)"
                  className={ModalStyle.transcriptAdditionInfo}
                />
                <Checkbox
                  name="additionalInfo_client"
                  input={{
                    value: this.state.ifHaveClientInfo,
                    onChange: () => this.toggleVisitorInfo('4'),
                  }}
                  text="Client Info (Operating System, Browser, Flash Version,
                  Screen Resolution, Language, Time Zone)"
                  className={ModalStyle.transcriptAdditionInfo}
                />
                <Checkbox
                  name="additionalInfo_rating"
                  input={{
                    value: this.state.ifHaveRating,
                    onChange: () => this.toggleVisitorInfo('5'),
                  }}
                  text="Rating (Rating, Comment)"
                  className={ModalStyle.transcriptAdditionInfo}
                />
                <Checkbox
                  name="additionalInfo_agentComment"
                  input={{
                    value: this.state.ifHaveAgentComment,
                    onChange: () => this.toggleVisitorInfo('6'),
                  }}
                  text="Agent Comment"
                  className={ModalStyle.transcriptAdditionInfo}
                />
              </div>
            :
            ''
          }
        </div>
        <div
          className={
              this.state.ifBodyLoading && !contentState.hasText() ?
              classnames('RichEditor-root', ModalStyle.editorDisable) :
              'RichEditor-root'}
        >
          <Editor
            editorState={editorState}
            onChange={this.onChange}
            placeholder=""
            spellCheck
          />
        </div>
      </div>
    );
  }
}

export default SendTranscriptBody;

SendTranscriptBody.propTypes = {
  input: React.PropTypes.shape({
    value: React.PropTypes.string,
    onChange: React.PropTypes.func,
  }),
  transcriptBodyAdditionInfo: React.PropTypes.shape({
    chatId: React.PropTypes.number,
    transcriptVisitorInfo: React.PropTypes.string,
    transcriptNavigation: React.PropTypes.string,
    transcriptGEOInfo: React.PropTypes.string,
    transcriptClientInfo: React.PropTypes.string,
    transcriptRating: React.PropTypes.string,
    transcriptAgentComment: React.PropTypes.string,
  }),
};



